*{
  margin: 0;
  padding: 0;
}
.container {
  margin: 0 auto;
  width: 80%;
  .title{
    text-align: center;
    margin-bottom:  20px;
  };
  nav {
    display: flex;
    justify-content:space-between;
    margin-bottom:  20px;
    button {
      background-color: #4CAF50; /* Green */
      border: none;
      border-radius:12px;
      color: white;
      padding: 15px 32px;
      text-align: center;
      text-decoration: none;
      font-size: 16px;
    }
  };
  form {
    font-size:20px;

    ul {
      li {
        margin-bottom:  15px;
        input {
          border: 2px solid #4CAF50;
        }

      }
    };

  };
  
 

}
